html,
body {
  margin: 0px;
}
::selection {
  color: #fff;
  background-color: transparent;
}

#viewport {
  background: #0e0d1f url("../../assets/images/slot/bg/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
}

.slot-container {
  width: 90%;
  margin: 0px auto;
  height: 90%;
}

#viewport.mobile {
  position: absolute;
  top: 0px;
  left: 0px;
}

#viewport.tablet {
  position: absolute;
  top: 0px;
  left: 0px;
}

#viewport.desktop {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}
#container {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
  background: url("../../assets/images/slot/bg/board.png");
  overflow: hidden;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

#container:after {
  content: "";
  display: none;
  position: absolute;
  width: 6%;
  height: 26%;
  top: 30%;
  right: -5.7%;
  transform: translateY(-30%);
  background: url("../../assets/images/slot/handle.png");
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.reel-content {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
}

#container #reels {
  position: relative;
  width: 60%;
  height: 65%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  margin: 0px auto;
  margin-top: -5%;
}

#container #reels .reel {
  width: 20%;
  height: 100%;
  position: relative;
  text-align: center;
  margin: 0px;
}

#container #reels .reel:nth-child(1) img {
  animation-duration: 3s;
  /* transition: 3s; */
}

#container #reels .reel:nth-child(2) img {
  /* transition: 5s; */
  animation-duration: 3.5s;
}

#container #reels .reel:nth-child(3) img {
  /* transition: 7s; */
  animation-duration: 4s;
}

#container #reels .reel:nth-child(4) img {
  /* transition: 7s; */
  animation-duration: 4.5s;
}

#container #reels .reel:nth-child(5) img {
  /* transition: 7s; */
  animation-duration: 5s;
}

#container #reels .reel img {
  width: 100%;
  margin: 0px auto;
}
.slot-items {
  margin: 2% auto;
  max-width: 65%;
  height: 24%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.reel-animation-end {
  transform: translateY(0px);
  transition: 0s !important;
}

.reel-transition {
  animation-name: reelAnimation;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
}

#container #overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
}

.winLine {
  width: 0px;
  height: 15px;
  position: absolute;
  background: white;
  border: 5px solid rgb(140, 246, 235);
  box-shadow: inset 0px 0px 2px rgb(140, 246, 235);
  border-radius: 7px;
  z-index: 1;
  transition: 1s;
  animation: 1s winlineAnime;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in-out;
  left: 68px;
}

#container #overlay #winLine1 {
  top: 93px;
}

#container #overlay #winLine2 {
  top: 210px;
}

#container #overlay #winLine3 {
  bottom: 94px;
}

#container #overlay #winLine4 {
  -webkit-transform: rotate(27deg);
  -o-transform: rotate(27deg);
  -moz-transform: rotate(27deg);
  -ms-transform: rotate(27deg);
  transform: rotate(27deg);
  top: 210px;
  left: 92px;
  width: 670px !important;
}

#container #overlay #winLine5 {
  -webkit-transform: rotate(152deg);
  -o-transform: rotate(152deg);
  -moz-transform: rotate(152deg);
  -ms-transform: rotate(152deg);
  transform: rotate(152deg);
  top: 215px;
  left: 55px;
  width: 683px !important;
  /* display: none; */
}

#btnSpin {
  /* background: url("../../assets/images/slot/spin.png"); */
  background: transparent;
  width: 20%;
  height: 15%;
  position: absolute;
  bottom: 2%;
  right: 7%;
  background-size: contain;
  cursor: pointer;
  background-repeat: no-repeat;
  text-align: center;
  background-position: center;
  transition: all 0.2s;
  z-index: 9;
}

