.btnSpinD {
  background: url("../../assets/images/slot/img/BTN_Spin_d.png");
  border-right: 4px solid grey;
}

#balanceDiv {
  width: 100%;
  position: relative;
  bottom: 10%;
  left: 0;
  height: 10%;
}

.custom-select-option {
  position: relative;
}

.custom-select-option h6 {
  background: transparent;
  cursor: pointer;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
}

.select-option {
  background: #051826;
  padding: 5px;
  border-radius: 5px;
  max-height: 155px;
  overflow: auto;
  position: absolute;
  max-width: 100%;
  width: 100%;
  top: -160px;
}

.win-amount {
  width: 15%;
  left: 20%;
  position: absolute;
  top: 2%;
}
.win-amount-box {
  display: flex;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
  justify-content: center;
  color: #fff;
  align-items: center;
}

.win-amount-box img {
  width: 30px;
  margin-right: 10px;
}
.bet-amount {
  width: 20%;
  left: 40%;
  position: absolute;
}

#balanceDiv input {
  width: 100%;
  background: transparent;
  border-radius: 5px;
  padding: 6px 20px;
  color: #fff;
  border: none;
  margin: 0;
  text-align: center;
  font-size: 34px;
  font-weight: bold;
}

#balanceDiv input:focus,
#balanceDiv input:focus-within {
  outline: none;
}

#balanceDiv .close-option {
  width: 16%;
  background: transparent;
  border-radius: 5px;
  padding: 8px 20px;
  color: #fff;
  border: none;
  margin: 0;
  left: 14%;
  position: absolute;
  top: 10%;
}

.select-option p {
  margin-bottom: 0px;
  padding: 10px 5px;
  color: white;
  font-size: 17px;
  border-bottom: 1px solid #102636;
  cursor: pointer;
  font-weight: 500;
}
.select-option p:last-child {
  border-bottom: none;
}
.select-option::-webkit-scrollbar {
  width: 5px;
}

.select-option::-webkit-scrollbar-track {
  background: #181d22;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.select-option::-webkit-scrollbar-thumb {
  background: #222c37;
}
.select-option p img {
  height: 20px;
  width: 20px;
  margin-right: 10px;
  border-radius: 50%;
}
.custom-select-option img.icon-img {
  height: 30px;
  width: 30px;
  margin-right: 8px;
  border-radius: 50%;
}

.custom-select-option h6 img {
  height: 30px;
  cursor: pointer;
  transition: all 0.5s;
}

.custom-select-option h6 .active-true + img {
  transform: rotate(-180deg);
}

/************************* Copyright Logo **********************/

.logo-watermark {
  position: absolute;
  bottom: 1%;
  left: 0.5%;
  width: 5%;
  display: none;
}

.logo-watermark img {
  width: 100%;
}

/************************* Animations **************************/

@keyframes reelAnimation {
  0% {
    transform: translateY(0px);
  }

  10% {
    transform: translateY(20px);
  }

  100% {
    transform: translateY(-4170px);
  }
}

@keyframes winlineAnime {
  from {
    width: 0px;
  }
  to {
    width: 710px;
  }
}

@keyframes backlight {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
